export function serverResponse(json) {
    const request = require("request");
    const data = request({
        'url': 'https://tensy.org/',
        'method': "POST",
        'json': json
    }, function(error, response, body) {
        if (!error && response.statusCode === 200) {
            return body;
        } else {
            console.log("error: " + error);
            console.log("response.statusCode: " + response.statusCode);
            console.log("response.statusText: " + response.statusText);
        }
    });
    return data;
}


export function getData(id, value, name) {
    return "https://playground24.ru/dsxt-api/get_data.php?id=" + id + "&value=" + value + "&name=" + name;
}
